@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "PTMono Regular";
  font-display: swap;
  src: url("../fonts/ptmono-regular.woff2") format("woff2"), url("../fonts/ptmono-regular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 500;
  font-family: "Roboto Medium";
  font-display: swap;
  src: url("../fonts/roboto-medium.woff2") format("woff2"), url("../fonts/roboto-medium.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Roboto Condensed Bold";
  font-display: swap;
  src: url("../fonts/roboto-condensed-bold.woff2") format("woff2"), url("../fonts/roboto-condensed-bold.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Roboto Condensed Regular";
  font-display: swap;
  src: url("../fonts/roboto-condensed–regular.woff2") format("woff2"), url("../fonts/roboto-condensed–regular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Rouble";
  font-display: swap;
  src: url("../fonts/rouble.woff2") format("woff2"), url("../fonts/rouble.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Placeholder", "Arial", sans-serif;
  color: #000000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.container {
  max-width: 1220px;
  margin: 0 auto;
}
@media (max-width: 1439px) {
  .container {
    padding: 0 32px;
  }
}
@media (max-width: 1023px) {
  .container {
    padding: 0 24px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 16px;
  }
}

.button {
  font-family: "Roboto Condensed Bold", "Arial", sans-serif;
  font-weight: 700;
  border-radius: 3px;
  color: #e5e5e5;
}

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  background-color: rgba(0, 0, 0, 0.8);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1), transform 0.6s cubic-bezier(0.55, 0, 0.1, 1), visibility 0.6s cubic-bezier(0.55, 0, 0.1, 1);
  pointer-events: none;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.modal::-webkit-scrollbar {
  display: none;
}
.modal__wrapper {
  position: relative;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100%;
  padding: 60px 40px;
}
@media (max-width: 767px) {
  .modal__wrapper {
    padding: 60px 16px;
  }
}
.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}
.modal__content {
  position: relative;
  width: 600px;
  padding: 40px;
  background-color: #ffffff;
}
@media (max-width: 767px) {
  .modal__content {
    width: 100%;
    padding: 20px;
  }
}
.modal__close-btn {
  position: absolute;
  top: 22px;
  right: 22px;
  z-index: 1;
  width: 40px;
  height: 40px;
  padding: 0;
  background: rgba(255, 255, 255, 0);
  border: none;
  cursor: pointer;
}
.modal--responsive .modal__content {
  width: 100%;
}
.modal--fit-content .modal__content {
  width: auto;
}
@media (max-width: 767px) {
  .modal--fit-content .modal__content {
    width: 100%;
  }
}
.modal--no-scale {
  transform: none;
}
.modal--preload {
  transition: none;
}
.modal.is-active {
  transform: scale(1);
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}

.promo {
  padding-bottom: 280px;
  background: url("../../img/promo-bg.jpg") center center/cover no-repeat;
  background-color: #041827;
}
@media (max-width: 1023px) {
  .promo {
    padding-bottom: 208px;
    background-position: -123px 0;
  }
}
@media (max-width: 767px) {
  .promo {
    padding-bottom: 190px;
    background-position: -227px 0;
  }
}
.promo__wrapper {
  display: flex;
  flex-direction: column;
  padding: 305px 0 0 134px;
}
@media (max-width: 1023px) {
  .promo__wrapper {
    padding: 180px 0 0 24px;
  }
}
@media (max-width: 767px) {
  .promo__wrapper {
    padding: 150px 0 0 9px;
  }
}
.promo__title {
  font-family: "Roboto Condensed Bold", "Arial", sans-serif;
  font-size: 80px;
  font-weight: 700;
  line-height: 94px;
  color: #f2f5f6;
  letter-spacing: 0.07em;
  text-transform: uppercase;
  margin: 0;
  padding-bottom: 32px;
}
@media (max-width: 1023px) {
  .promo__title {
    font-size: 64px;
    line-height: 75px;
    padding-bottom: 0;
  }
}
@media (max-width: 767px) {
  .promo__title {
    font-size: 36px;
    line-height: 42px;
    padding-bottom: 8px;
  }
}
.promo__text {
  font-family: "PTMono Regular", "Arial", sans-serif;
  font-size: 22px;
  font-weight: 400;
  line-height: 34px;
  color: #f2f5f6;
  margin: 0;
  padding-bottom: 47px;
}
@media (max-width: 1023px) {
  .promo__text {
    padding-bottom: 58px;
  }
}
@media (max-width: 767px) {
  .promo__text {
    font-size: 14px;
    line-height: 21px;
    padding-bottom: 45px;
  }
  .promo__text br {
    display: none;
  }
}
.promo__button {
  width: 410px;
  font-size: 18px;
  font-weight: 700;
  line-height: 21px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  padding: 16px 25px;
  background-color: #00b2ff;
  border: none;
}
.promo__button--mobile {
  display: none;
}
@media (max-width: 1023px) {
  .promo__button {
    margin-left: 46px;
  }
}
@media (max-width: 767px) {
  .promo__button {
    width: 270px;
    display: none;
    margin: 0;
    font-size: 14px;
    line-height: 16px;
    padding: 12px 25px;
  }
  .promo__button--mobile {
    display: block;
  }
}

.header {
  position: fixed;
  width: 100%;
  top: 0;
  padding: 28px 0 35px;
  background: linear-gradient(180deg, #041827 84.9%, rgba(4, 24, 39, 0) 100%);
}
@media (max-width: 1023px) {
  .header {
    padding: 20px 0 25px;
  }
}
@media (max-width: 767px) {
  .header {
    padding: 15px 0 20px;
  }
}
@media (max-width: 1023px) {
  .header__menu {
    padding: 0 50px 0;
  }
}
@media (max-width: 767px) {
  .header__menu {
    padding: 0 25px 0;
  }
}

.menu {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  padding: 0 54px 0 7px;
}
.menu__contacts {
  width: 460px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}
@media (max-width: 1023px) {
  .menu__contacts {
    width: 93px;
    padding: 0 13px 16px 0;
  }
}
@media (max-width: 767px) {
  .menu__contacts {
    width: 85px;
    margin: 0;
    padding: 0 6px 3px 0;
  }
}
.menu__text {
  font-family: "PTMono Regular", "Arial", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 18px;
  color: #f2f5f6;
  padding-left: 15px;
}
@media (max-width: 1023px) {
  .menu__text {
    display: none;
  }
}
.menu__button {
  width: 160px;
  font-size: 14px;
  line-height: 17px;
  text-transform: uppercase;
  border: 1px solid #f2f5f6;
  padding: 10px 15px;
  background-color: transparent;
}
@media (max-width: 1023px) {
  .menu__button {
    display: none;
  }
}
.menu__email-icon, .menu__phone-icon {
  width: 20px;
  height: 20px;
}

.menu__logo {
  width: 215px;
}
@media (max-width: 767px) {
  .menu__logo {
    width: 35px;
  }
}
.menu__logo img {
  width: 100%;
}

.menu a {
  display: flex;
  align-items: center;
}

.features {
  background-color: #ffffff;
  margin: 0;
  padding-top: 120px;
}
.features__list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(595px, 1fr));
  grid-gap: 30px;
}
.features__item {
  min-height: 300px;
  margin: 0;
  padding: 40px 19px 0 43px;
  border: 1px solid #e5e5e5;
  border-left: 5px solid #00b2ff;
}
.features__item-icon {
  width: 35px;
  height: 35px;
}
.features__heading {
  font-family: "Roboto Condensed Regular", "Arial", sans-serif;
  font-size: 24px;
  font-weight: 400;
  line-height: 34px;
  color: #00b2ff;
  margin: 0;
  padding: 14px 0 14px;
}
.features__text {
  font-family: "PTMono Regular", "Arial", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 22px;
  color: #041827;
  margin: 0;
}

.features__item:hover {
  background-color: #00b2ff;
}
.features__item:hover * {
  color: white;
}

